#burger, [for="burger"] {
  visibility: hidden;
}

@media screen and (max-width: 550px) {
  .burg {
    visibility: visible;
    width: 30px;
    text-align: center;
    position: absolute;
    top: 27px;
    right: 33px;
  }

  .burg:before {
    content: "☰";
    font-size: 30px;
  }

  ul {
    height: 10.5rem;
    background-color: #4394db;
    border-radius: 5px;
    padding-right: 2rem;
    position: absolute;
    top: 70px;
    right: 150px;
  }

  .items {
    position: absolute;
    top: 130px;
    left: -60px;
  }

  #burger:checked + label + ul {
    animation: action .7s;
    display: block;
  }

  #burger:checked + [for="burger"]:before {
    content: "×";
    text-align: center;
    width: 50px;
    height: 50px;
    background-color: #7fffd4;
    border: 2px solid;
    border-radius: 50%;
    font-size: 30px;
    animation: spin 1.3s linear infinite;
    display: block;
  }

  @keyframes action {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  @keyframes spin {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
}

